<template>
  <div class="deploy">
    <p>独立部署授权管理</p>
    <div class="nav">
      <div class="nav-top">
        <div>
          <span>合作状态</span>
          <el-select v-model="cooperateValue" placeholder="请选择">
            <el-option
              :options="cooperate"
              v-for="item in cooperate"
              :key="item.cooperateValue"
              :label="item.label"
              :value="item.cooperateValue"
            ></el-option>
          </el-select>
        </div>
        <div class="area">
          <span class="demonstration">地区</span>
          <el-cascader v-model="area" :options="areaOption" @change="handleChange"></el-cascader>
        </div>
        <div class="timeBetween">
          <span>到期时间</span>
          <el-date-picker
            v-model="value2"
            type="date"
            placeholder="选择日期"
            @change="date1"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
          <span class="to">至</span>
          <el-date-picker
            v-model="value4"
            type="date"
            placeholder="选择日期"
            @change="date2"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </div>
        <div>
          <span>检索</span>
          <el-input v-model="value3" placeholder="请输入企业名称"></el-input>
        </div>
        <button @click="search">查询</button>
        <button @click="add">添加第三方企业</button>
      </div>
      <div class="nav-bottom">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="bname" label="企业名称"></el-table-column>
          <el-table-column prop="address" label="地区"></el-table-column>
          <el-table-column prop="validity_time" label="服务器到期时间"></el-table-column>
          <el-table-column prop="state" label="状态" :formatter="changeStatus"></el-table-column>
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="check(scope.row.bid)">查看</el-button>
              <el-button type="text" size="small" @click="update(scope.row.bid)">编辑</el-button>
              <el-button type="text" size="small" @click="createLicense(scope.row)">生成</el-button>
              <!-- <el-button type="text" size="small" @click="downloadLicense(scope.row)">下载</el-button> -->
              <a  :href="licenseUrl" class="tabA" @click="downloadLicense(scope.row)">下载</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="pageNum"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { regionDataPlus, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      area: [],
      areaOption: regionDataPlus,
      cooperateValue: "",
      cooperate: [
        {
          cooperateValue: "",
          label: "全部"
        },
        {
          cooperateValue: 0,
          label: "合作中"
        },
        {
          cooperateValue: 1,
          label: "暂停合作"
        }
      ],
      options2: [
        {
          value2: "选项1",
          label: "企业名称"
        },
        {
          value2: "选项2",
          label: "主账号"
        }
      ],

      value2: "",
      value3: "",
      value4: "",
      flag: false,
      form2: {
        dateTime: ""
      },
      formLabelWidth: "120px",
      coop: "",
      licenseUrl:""
    };
  },
  mounted() {
    this.deployFormAsync();
  },
  computed: {
    ...mapState([
      "pageNum",
      "pageSize",
      "list",
      "total",
      "thirdBussinessManager"
    ])
  },
  methods: {
    ...mapActions([
      "deployFormAsync",
      "changePageAsync",
      "searchAsync",
      "generateLicenseAsync"
    ]),
    add() {
      this.$router.push(`/Index/others`);
    },
    check(prop) {
      this.$router.push(`/Index/otherDetails`);
      sessionStorage.setItem("threeDetailID", JSON.stringify(prop));
    },
    update(prop) {
      this.$router.push(`/Index/othersUpdate`);
      sessionStorage.setItem("otherUpdateID", JSON.stringify(prop));
    },
    //合作
    changeStatus(row, column) {
      switch (row.state) {
        case 0:
          return "合作中";
          break;
        case 1:
          return "暂停合作";
          break;
      }
    },

    //地区获取
    handleChange(value) {
      let province;
      let city;
      let area;
      // console.log(value[0]);
      if (CodeToText[value[0]] == "全部") {
        province = "";
        city = "";
        area = "";
        this.location = province + city + area;
      } else {
        province = CodeToText[value[0]];
        if (CodeToText[value[1]] == "全部") {
          city = "";
          area = "";
          this.location = province + city + area;
        } else {
          city = CodeToText[value[1]];
          if (CodeToText[value[2]] == "全部") {
            area = "";
            this.location = province + city + area;
          } else {
            area = CodeToText[value[2]];
            this.location = province + city + area;
          }
        }
      }
    },
    //到期时间
    date1(value) {
      this.startTime = value;
    },
    date2(value) {
      this.endTime = value;
    },
    //生成license
    async createLicense(prop) {
      let host = prop.server_host;
      let macs = prop.server_mac;
      let ids = prop.bid;
      let time = prop.validity_time;
      
      const data = await this.generateLicenseAsync({
        server_host: host,
        server_mac: macs,
        expiryTime: time,
        bid: ids
      });
      if (data.code == 200) {
        this.$message("license生成成功");
        this.deployFormAsync();
      } else {
        this.$message("license生成失败，请重试或联系管理员");
      }
    },
    //下载license
    downloadLicense(prop) {      
      this.downAdress=prop.password;
      if (prop.password=='') {
        this.$message("未生成license文件，请先生成");
        this.licenseUrl=null;
      } 
      else {
        this.licenseUrl=this.$download+this.downAdress;
      }
      
      
    },
    //查询
    search() {
      let location = this.location;
      let start = this.startTime;
      let end = this.endTime;
      let coop = this.cooperateValue;
      let companyName = this.value3;
      this.$store.commit("deploy/initLocation", location);
      this.$store.commit("deploy/initStart", start);
      this.$store.commit("deploy/initEnd", end);
      this.$store.commit("deploy/initCoop", coop);
      this.$store.commit("deploy/initWriteName", companyName);
      this.searchAsync();
    },
    //分页
    handleCurrentChange(val) {
      this.$store.commit("deploy/changePage", val);
      this.deployFormAsync();
    }
  }
};
</script>

