import axios from "axios";
import qs from "qs";

export default {
    //查询所以数据并分页
    async companyForm(params) {
        let { data } = await axios.post("/enterprise/selectAllBusinessManager", qs.stringify(params));
        return data;
    },
    //企业管理按条件筛选查询
    async search(params) {
        let { data } = await axios.post("/enterprise/selectEnterprise", qs.stringify(params));
        return data;
    },
    //企业明细查看
    async companyDetail(params) {
        let { data } = await axios.post("/enterprise/selectEnterpriseDetail", qs.stringify(params));
        return data;
    },
    //企业合作状态操作-重启合作
    async reStart(params) {
        let { data } = await axios.post("/enterprise/restartOperation", qs.stringify(params));
        return data;
    },
    //企业合作状态操作-暂停合作
    async pause(params) {
        let { data } = await axios.post("/enterprise/pauseOperation", qs.stringify(params));
        return data;
    },
    //获取省市区列表
    async location(params) {
        let { data } = await axios.post("/enterprise/getSubList", qs.stringify(params));
        return data;
    },
    //重置密码
    async rePassword(params) {
        let { data } = await axios.get("/enterprise/restPwd", { params });
        return data;
    },
    //添加企业
    async addition(params) {
        let { data } = await axios.post("/enterprise/addEnterprise", qs.stringify(params));
        return data;
    },
    //修改企业明细
    async updateCompany(params) {
        let { data } = await axios.post("/enterprise/editEnterpriseDetail", qs.stringify(params));
        return data;
    },
    //企业统计查询
    async statistic(params) {
        let { data } = await axios.post("/remote/selectBusinessStatistics", qs.stringify(params));
        return data;
    },
    //查询企业储存空间
    async companyStorage(params) {
        let { data } = await axios.post("/enterprise/handleQueryEnterpriseStorageSpace", qs.stringify(params));
        return data;
    },
    //清除企业存储空间
    async clearStorage(params) {
        let { data } = await axios.post("/enterprise/handleDeleteEnterpriseStorageSpaceByCondition", qs.stringify(params));
        return data;
    },
    //判断主账号是否重复
    async judgement(params) {
        let { data } = await axios.get("/enterprise/handleQueryAccount", { params });
        return data;
    },
}