import centerApi from "../services/centerService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 4,
        status: '',
        list: [],
        total: 4,
        bname: "",
        backgroundUrl: '',
    },
    mutations: {
        //全部
        allData(state, payload) {
            Object.assign(state, {
                list: payload.allBusiness.list,
                total: payload.allBusiness.total,
            })
        },
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },
        initStatus(state, payload) {
            Object.assign(state, {
                pageNum: 1,
                status: payload
            })
        },
        initSearch(state, payload) {
            Object.assign(state, {
                bname: payload,
                pageNum: 1,
            })
        },
        //首页--通过名字模糊查询
        query(state, payload) {
            console.log(payload);
            Object.assign(state, {
                list: payload.business
            })
        },
        //logo和meta的url
        backgroundUrl(state, payload) {
            state.backgroundUrl = payload;
        }
    },
    actions: {
        //修改姓名
        async changeNameAsync(context, payload) {
            const { data } = await centerApi.changeName(payload);
            return data;
        },
        //修改密码
        async changePasswordAsync(context, payload) {
            const data = await centerApi.changePassword(payload);
            return data;
        },
        //列表全部数据获取
        async allCompanyAsync(context) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                status: context.state.status,
                bname: context.state.bname
            };
            const { data } = await centerApi.allCompany(params);
            context.commit("allData", data)
            return data;
        },
        //首页--通过名字模糊查询
        async searchAsync(context, payload) {
            const { data } = await centerApi.search(payload)
            context.commit("query", data)
            return data;
        },
        //首页--通过id进行企业统计查询
        async allChartsAsync(context, payload) {
            const { data } = await centerApi.allCharts(payload)
            return data;
        },
        //文件上传
        async uploadFileAsync(context, payload) {
            const data = await centerApi.uploadFile(payload);
            return data;
        },
        //首页获取所有经纬度
        async allLongitudeAsync(context, payload) {
            const { data } = await centerApi.allLongitude(payload)
            return data;
        },
        //获取logo和meta
        async logoMeta(context, payload) {
            const { data } = await centerApi.getLogeMeta()
            sessionStorage.setItem('metaUrl', data.data.metaUrl)
            context.commit('backgroundUrl', data);
        },
        //消息中心状态
        async newsStatusAsync(context, payload) {
            const { data } = await centerApi.newsStatus(payload)
            return data;
        },
    }
}