import appUpdateApi from "../services/appUpdateService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 6,
        list: [],
        total: 4,
        taskType: "",
        startTime: "",
        endTime: "",
        terminalApp: {},
        dispatcherApp: {}
    },
    mutations: {
        setData(state, payload) {
            //total整合总条数
            Object.assign(state, {
                list: payload.logList.list,
                total: payload.logList.total
            });
        },
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            });
        },
        initTaskValue(state, payload) {
            Object.assign(state, {
                taskType: payload
            });
        },
        initStartTime(state, payload) {
            Object.assign(state, {
                startTime: payload
            });
        },
        initEndTime(state, payload) {
            Object.assign(state, {
                endTime: payload
            });
        },
        setTerminal(state, payload) {
            state.terminalApp = payload;
        },
        setDispatcher(state, payload) {
            state.dispatcherApp = payload;
        }
    },
    actions: {
        //文件上传终端
        async uploadTerminalAsync(context, payload) {
            // console.log('请求',payload);
            const data = await appUpdateApi.uploadFile(payload);
            // console.log(data)

            context.dispatch("searchAppAsync");
            return data;
        },
        //文件上传调度员
        async uploadDispatcherAsync(context, payload) {
            const data = await appUpdateApi.uploadFile(payload);
            context.dispatch("searchAppAsync");
            return data;
        },
        //获取app更新设置
        async searchAppAsync(context) {
            let data = await appUpdateApi.searchApp();
            data.data.forEach(el => {
                if (el.id === 2) {
                    //终端
                    context.commit("setDispatcher", el);
                } else {
                    context.commit("setTerminal", el);
                }
            });
        },
        //列表数据获取
        async logFormAsync(context, payload) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                taskType: context.state.taskType,
                startTime: context.state.startTime,
                endTime: context.state.endTime
            };
            const { data } = await appUpdateApi.search(params);
            context.commit("setData", data);
            return data;
        },
        //分页
        async changePageAsync(context, payload) {
            context.commit("changePage", payload);
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                taskType: context.state.taskType,
                startTime: context.state.startTime,
                endTime: context.state.endTime
            };
            const { data } = await appUpdateApi.search(params);
            context.commit("setData", data);
        }
        //日志导出
        // async exportLogAsync(context,payload){
        //     const data =await appUpdateApi.exportLog(payload);
        //     return data;
        // },
    }
};