import axios from "axios";
import qs from "qs";

export default {
    //查询所以数据并分页
    async deployForm(params) {
        let { data } = await axios.post("/authorizationManagement/handleQueryAllTBusinessManager", qs.stringify(params));
        return data;
    },
    //重启第三方企业合作
    async reStart(params) {
        let { data } = await axios.post("/authorizationManagement/restartTBusinessManager", qs.stringify(params));
        return data;
    },
    //暂停第三方企业合作
    async pause(params) {
        let { data } = await axios.post("/authorizationManagement/pauseTBusinessManager", qs.stringify(params));
        return data;
    },
    //第三方企业查询 按条件
    async search(params) {
        let { data } = await axios.post("/authorizationManagement/getTBusinessManagerList", qs.stringify(params));
        return data;
    },
    //第三方企业详情查看
    async thirdDetail(params) {
        let { data } = await axios.post("/authorizationManagement/TBusinessManagerSelect", qs.stringify(params));
        return data;
    },
    //添加第三方企业
    async addThird(params) {
        let { data } = await axios.post("/authorizationManagement/addManagement", qs.stringify(params));
        return data;
    },
    //编辑更新第三方企业
    async updateThird(params) {
        let { data } = await axios.post("/authorizationManagement/TBusinessManagerUpdate", qs.stringify(params));
        return data;
    },
    //生成许可证
    async generateLicense(params) {
        let { data } = await axios.get("/license/generateLicense", { params });
        return data;
    },
}