<template>
  <div class="othersContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/deploy' }">独立部署授权管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加第三方企业</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form
        :model="ruleForm"
        ref="ruleForm"
        :rules="rules"
        label-width="240px"
        class="demo-ruleForm"
      >
        <el-form-item label="企业名称" prop="bname">
          <el-input v-model="ruleForm.bname" placeholder="限20字"></el-input>
        </el-form-item>
        <!-- <el-form-item label="主账号" prop="mainAccount">
          <el-input v-model="ruleForm.mainAccount" placeholder="3-10位数字、字母、字符"></el-input>
        </el-form-item>
        <p class="ptips">主账号添加后不可修改，且不可重复，初始密码与账号相同</p>-->
        <el-form-item label="企业地址" prop="address">
          <label>
            <el-cascader
              v-model="ruleForm.area"
              :options="areaOption"
              @change="handleChange"
              clearable
            ></el-cascader>
          </label>
          <el-input
            v-model="ruleForm.address"
            placeholder="请输入详细地址"
            id="suggestId"
            style="marginTop:15px;"
          ></el-input>
        </el-form-item>
        <el-form-item style="marginTop:-15px;">
          <baidu-map
            :center="center"
            :zoom="zoom"
            @ready="handler"
            class="baiduMap"
            :scroll-wheel-zoom="true"
            id="l-map"
          >
            <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
            <bm-local-search
              :keyword="ruleForm.address"
              :auto-viewport="true"
              :panel="false"
              @searchcomplete="getGps"
            ></bm-local-search>
          </baidu-map>
        </el-form-item>
        <el-form-item label="负责人" prop="personInCharge">
          <el-input v-model="ruleForm.personInCharge" placeholder="限20字"></el-input>
        </el-form-item>
        <el-form-item label="负责人电话" prop="picPhone">
          <el-input v-model="ruleForm.picPhone"></el-input>
        </el-form-item>

        <el-form-item label="服务到期时间" prop="validityTime">
          <el-date-picker
            v-model="ruleForm.validityTime"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="合作状态" prop="state">
          <el-select v-model="ruleForm.state" placeholder="请选择合作状态">
            <el-option label="合作中" value="0"></el-option>
            <el-option label="暂停合作" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="其他说明" prop="description">
          <el-input
            type="textarea"
            v-model="ruleForm.description"
            placeholder="限500字"
            maxlength="500"
          ></el-input>
        </el-form-item>
        <el-form-item label="绑定服务器IP" prop="serverHost">
          <el-input v-model="ruleForm.serverHost"></el-input>
        </el-form-item>
        <el-form-item label="绑定服务器MAC地址" prop="serverMac">
          <el-input v-model="ruleForm.serverMac"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
import { BmlMarkerClusterer } from "vue-baidu-map";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        // mainAccount: "",
        area: "",
        address: "",
        personInCharge: "",
        picPhone: "",
        state: "",
        description: "",
        serverHost: "",
        serverMac: "",
        validityTime: "",
      },
      areaOption: regionData,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      location: "北京",
      keyword: "百度",
      arealocation: "",
      rules: {
        bname: [
          { required: true, message: "请输企业名称", trigger: "blur" },
          { min: 1, max: 20, message: "长度不能超过20个字符", trigger: "blur" },
        ],
        // mainAccount: [
        //   { required: true, message: "请输入主账号", trigger: "blur" },
        //   {
        //     min: 3,
        //     max: 10,
        //     message: "3-10位数字、字母、字符",
        //     trigger: "blur"
        //   }
        // ],
        area: [{ required: true, message: "请选择省市区", trigger: "blur" }],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" },
        ],
        personInCharge: [
          { required: true, message: "请输入负责人", trigger: "blur" },
        ],
        picPhone: [
          { required: true, message: "请输入负责人电话", trigger: "blur" },
        ],
        validityTime: [
          { required: true, message: "请选择服务到期时间", trigger: "change" },
        ],
        state: [
          { required: true, message: "请选择合作状态", trigger: "change" },
        ],
        description: [{ max: 500, message: "限500字", trigger: "blur" }],
        serverHost: [
          { required: true, message: "请输入服务器IP", trigger: "blur" },
        ],
        serverMac: [
          { required: true, message: "请输入服务器MAC地址", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    ...mapActions(["addThirdAsync", "deployFormAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.arealocation = province + city + area;
      // console.log(this.arealocation);
      this.ruleForm.address = this.arealocation;
      // console.log(this.ruleForm.address);
    },
    async add() {
      let name = this.ruleForm.bname;
      let locations = this.ruleForm.address;
      let lat = this.gpsLocation.lat;
      let lon = this.gpsLocation.lng;
      let charge = this.ruleForm.personInCharge;
      let phone = this.ruleForm.picPhone;
      let dateTime = this.ruleForm.validityTime + " " + "00:00:00";
      let states = this.ruleForm.state;
      let describe = this.ruleForm.description;
      let hostSer = this.ruleForm.serverHost;
      let macSer = this.ruleForm.serverMac;
      let form = {
        bname: name,
        address: locations,
        personInCharge: charge,
        picPhone: phone,
        validityTime: dateTime,
        state: states,
        description: describe,
        serverHost: hostSer,
        serverMac: macSer,
        longitude: lon,
        latitude: lat,
      };
      const data = await this.addThirdAsync(form);
      if (data.code == 200) {
        this.$message("添加成功");
        this.deployFormAsync();
        this.$router.push(`/Index/deploy`);
      } else if (data.code == 202) {
        this.$message("用户名已经存在");
      }
    },
    //百度地图
    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);
    },
    getGps(results) {
      // console.log(results);
      // console.log(results);
      // if (results !== "undefined") {
      this.gpsLocation = results.Hr[0].point;
      // }
    },
  },
};
</script>

