import Vue from 'vue'
import App from './App.vue'
import ElementUI, {
    Message
} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import router from './router'
import 'lib-flexible/flexible.js'
import _g from "./_globals"
import "./httpRequest.js"
import echarts from "echarts";
import "echarts/lib/chart/bar";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/title";
import "echarts/map/js/china.js"
import "echarts/map/js/world.js"
import "echarts/lib/component/grid";
import hljs from 'highlight.js';
import 'highlight.js/styles/monokai.css';
import store from "./store.js";
import qs from "qs";

import {
    provinceAndCityData,
    regionData,
    provinceAndCityDataPlus,
    regionDataPlus,
    CodeToText,
    TextToCode
} from 'element-china-area-data';
import BaiduMap from "vue-baidu-map";
import axios from 'axios'
import {
    MessageBox
} from "element-ui"

Vue.config.productionTip = false;
Vue.use(ElementUI);
//图表
Vue.prototype.$echarts = echarts;

Vue.prototype.$download = '';
Vue.prototype.$http = axios
    //代码高亮
Vue.directive('highlight', function(el) {
    let blocks = el.querySelectorAll('pre code');
    blocks.forEach((block) => {
        hljs.highlightBlock(block)
    })
});
//百度地图
Vue.use(BaiduMap, {
        ak: '77VVGVwqMGUlQwlmBwp5nVgMTwFu06YN'
    })
    // http response 拦截器
axios.interceptors.response.use(
    res => {
        if (res.data.code == 666) {
            MessageBox.alert('登录信息超时，请重新登录', {
                confirmButtonText: "跳转登录页面",
                callback: action => {
                    router.push({
                        path: '/Login'
                    })
                    return res
                }
            })

        } else {
            return res;
        }
        return res;
    },
);


// 监听页面刷新
window.onload = function(e) {
    if (sessionStorage.getItem("metaUrl") !== null) {
        var link = document.querySelector("link[rel*='icon']") || document.createElement('link');
        link.type = 'image/x-icon';
        link.rel = 'icon';
        link.href = '' + sessionStorage.getItem("metaUrl");
        document.getElementsByTagName('head')[0].appendChild(link);
    }
};


new Vue({
    el: "#app",
    router,
    store,
    render: h => h(App),
})